import React, { Component } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "shards-ui/dist/css/shards.min.css";

import FighterInfo from "./detail";

import axios from "axios";

import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  FormSelect,
  Container,
  Row,
  Col,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  FormInput,
  Collapse,
  Button,
} from "shards-react";

export default class HomePage extends React.Component {
  constructor(props) {
    super(props);

    this.toggleDropdown = this.toggleDropdown.bind(this);
    this.toggleNavbar = this.toggleNavbar.bind(this);

    this.state = {
      dropdownOpen: false,
      collapseOpen: false,
      selectedFighter: "Aiden",
      showFighter: false,
      users: [],
    };
  }
  componentDidMount() {
    var usersGet = [];
    axios
      .get(`http://3.1.218.91:8000/get_by/users`)
      .then((res) => {
        usersGet = res.data.data;
        console.log(usersGet);
        this.setState({
          ...{
            users: usersGet,
            selectedFighter: usersGet[0],
          },
        });
      })
      .catch((error) => console.log(error));
  }

  toggleDropdown() {
    this.setState({
      ...this.state,
      ...{
        dropdownOpen: !this.state.dropdownOpen,
      },
    });
  }

  toggleNavbar() {
    this.setState({
      ...this.state,
      ...{
        collapseOpen: !this.state.collapseOpen,
      },
    });
  }

  handleDrop(e) {
    console.log("Updated to " + e.target.value);

    this.setState({
      ...this.state,
      ...{
        selectedFighter: e.target.value,
      },
    });
  }

  render() {
    const normalHome = (
      <div>
        <div style={{ margin: "100px" }}></div>
        <h2>Select your fighter</h2>
        <div style={{ margin: "100px" }}></div>
        <Container style={{ display: "flex", justifyContent: "space-between" }}>
          {/* Dropdown to select user */}
          <Row>
            <FormSelect
              size="sm"
              className="mb-2"
              onChange={(e) => {
                //Selected fighter
                this.handleDrop(e);
              }}
            >
              {this.state.users.map(function (e, id) {
                return <option value={e}>{e}</option>;
              })}
            </FormSelect>
            <Button
              theme="success"
              onClick={() => {
                // if (this.state.selectedFighter != "Select Fighter")
                this.setState({
                  showFighter: true,
                });
              }}
            >
              Go
            </Button>
          </Row>
        </Container>
      </div>
    );

    const showInfo = (
      <div>
        <div style={{ margin: "100px" }}></div>
        <h2>Fighter info</h2>
        <div style={{ margin: "100px" }}></div>
      </div>
    );

    return (
      <div>
        <Navbar type="dark" theme="primary" expand="md">
          <NavbarBrand href="#">CAAS Controller Dashboard</NavbarBrand>
          <NavbarToggler onClick={this.toggleNavbar} />

          <Collapse open={this.state.collapseOpen} navbar>
            <Nav navbar>
              <NavItem>
                <NavLink active href="/">
                  Home
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="https://yitec.net" navbar>
                  YITEC
                </NavLink>
              </NavItem>
            </Nav>

            <Nav navbar className="ml-auto">
              <InputGroup size="sm" seamless>
                <InputGroupAddon type="prepend">
                  <InputGroupText>
                    <FontAwesomeIcon icon={faSearch} />
                  </InputGroupText>
                </InputGroupAddon>
                <FormInput className="border-0" placeholder="Search..." />
              </InputGroup>
            </Nav>
          </Collapse>
        </Navbar>
        {this.state.showFighter ? (
          <FighterInfo id={this.state.selectedFighter}></FighterInfo>
        ) : (
          normalHome
        )}
      </div>
    );
  }
}
