import React from "react";
import {
  Card,
  CardHeader,
  CardTitle,
  CardImg,
  CardBody,
  CardFooter,
  Button,
  FormInput
} from "shards-react";
import axios from 'axios';

export default class FighterInfo extends React.Component  {

  constructor(props){
    super(props)
    this.state = {
        FighterName: props.id,
        FighterImage: "https://place-hold.it/1200x600",
        FighterQuestion: null,
        FighterAnswer: null,
    }
  }
  componentDidMount() {
    console.log(`http://3.1.218.91:8000/get_by/`+this.state.FighterName)
    axios.get(`http://3.1.218.91:8000/get_by/`+this.state.FighterName)
      .then(res => {
        var usrdata = res.data.data;
        console.log(usrdata)
        //Sort
        usrdata.sort(function(y, x){
            return x.timestamp - y.timestamp;
        })
        console.log(usrdata[0].qid)
        this.setState(
            {
                ... {
                    FighterQuestion: usrdata[0].qid,
                    FighterImage: usrdata[0].img
                }
        })
        .catch(error => console.log(error));
      })
      .catch(error => console.log(error));
  }

  render(){
    return (
        <Card style={{ maxWidth: "100%" }}>
          <CardHeader>Fighter Info</CardHeader>
          <CardImg src={this.state.FighterImage} />
          <CardBody>
            <CardTitle>{this.state.FighterName}</CardTitle>
            <p>Help this fighter find his answer</p>
            <FormInput 
            onChange={(e)=>{
                this.setState({
                    ...{
                        FighterAnswer: e.target.value
                    }
                })
            }}
            placeholder="Submit answer" />
            <div style={{margin: "20px"}}></div>
            <Button onClick={()=>{
                axios.post(`http://3.1.218.91:8000/answer/${this.state.FighterQuestion}/${this.state.FighterAnswer}`).then(
                    res=>{
                        console.log(res.data)
                    }
                ).catch(error=>console.log(error))
            }}>Submit answer &rarr;</Button>
          </CardBody>
        </Card>
      );
  }
}